import styled from "styled-components";

export const Links = styled.div`
  img {
    width: 70px;
    height: 70px;
    margin: 0 2%;
  }
  display: flex;
  align-items: center;
  justify-content: center;
`;
export const ModalHeader = styled.div`
  display: flex;
  margin: 0 0 2% 0;
  h3 {
    margin-left: 4%;
  }
  img {
    width: 70px;
    height: 70px;
  }
`;

export const Wrapper = styled.div`
  text-align: end;
  .bx {
    font-size: 30px;
    cursor: pointer;
  }
`;
