import { Tab, Tabs } from "@mui/material";
import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { Col, Row } from "react-grid-system";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import { WrapperContainer } from "../../../App-styled";
import { GetCategory, GetProject } from "../../../store/client/addProect";
import News from "../news";
import { Header, Wrapper } from "./styled-index";
import "./styled.css";

function ReadyComp() {
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetCategory());
    dispatch(GetProject());
  }, []);
  const Category = useSelector(
    (state) => state?.addProject?.getCategory?.Data?.data
  );
  const data = useSelector(
    (state) => state?.addProject?.getProject?.Data?.data
  );

  const [value, setValue] = useState("");
  const [val, setVal] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(event.target.id);
    setVal(newValue);
  };
  return (
    <>
      <WrapperContainer>
        <Header>
          <h2>{t("Ready.0")}</h2>
          <p>{t("Ready.1")}</p>
        </Header>
        <Tabs
          value={val}
          onChange={handleChange}
          aria-label="basic tabs example"
        >
          {Category?.map((elem, index) => (
            <Tab key={index} id={elem.title} label={elem.title} />
          ))}
        </Tabs>
        <Wrapper>
          <Row style={{ margin: 0 }}>
            {value == "" ? (
              <>
                {data?.map((elem) => (
                  <Col lg={4} md={6} sm={12}>
                    <div className="cols">
                      <img src={elem.img} alt="photo" />
                      <div className="texts">
                        <h3>{elem?.categoryId?.title}</h3>
                        <p>{elem.company_name}</p>
                        <p>Возраст бизнеса - {elem.business_age} лет</p>
                        <p>Количество сотрудников - {elem.employees_number}</p>
                        <p>Окупаемость - {elem.payback} года</p>
                        <p>Причина продажи - {elem.reason_for_sale}</p>
                        <NavLink
                          to={"/more"}
                          onClick={() =>
                            window.localStorage.setItem("moreIDD", elem.id)
                          }
                        >
                          <button>{t("Search.3")}</button>
                        </NavLink>
                      </div>
                    </div>
                  </Col>
                ))}
              </>
            ) : (
              <>
                {data?.map((elem) =>
                  elem?.categoryId?.title == value ? (
                    <Col lg={4} md={6} sm={12}>
                      <div className="cols">
                        <img src={elem.img} alt="photo" />
                        <div className="texts">
                          <h3>{elem?.categoryId?.title}</h3>
                          <p>{elem.company_name}</p>
                          <p>Возраст бизнеса - {elem.business_age} лет</p>
                          <p>
                            Количество сотрудников - {elem.employees_number}
                          </p>
                          <p>Окупаемость - {elem.payback} года</p>
                          <p>Причина продажи - {elem.reason_for_sale}</p>
                          <button>{t("Search.3")}</button>
                        </div>
                      </div>
                    </Col>
                  ) : null
                )}
              </>
            )}
          </Row>

          {/* <div className="moreBtn">
            <div className="btn">
              <button>Посмотреть все</button>
            </div>
          </div> */}
        </Wrapper>
        <News />
      </WrapperContainer>
    </>
  );
}

export default ReadyComp;
