import { Wrapper ,WrapperTop , WrapperItem  ,WrapperList, MenuFooter} from "./styled-index"
import Logo from "./../../../../assets/images/client/header/logo.svg"
import Catalog from "./../../../../assets/images/client/header/close-icon-catalog.png"
import UzFlag from "./../../../../assets/images/client/header/uzbekistan-flag-icon.svg"
import RuFlag from "./../../../../assets/images/client/header/russian-flag.png"
import EnFlag from "./../../../../assets/images/client/header/england-flag.png"

import { DataLink } from "../dataLink"
import { NavLink } from "react-router-dom"
import { t } from "i18next"
import { useTranslation } from "react-i18next"
function MediaMenu({menu , HandleClose}) {
    const { t, i18n } = useTranslation();
    const handleLang = (e) =>{
        const lang = e.target.value
        i18n.changeLanguage(lang)
        window.location.reload()
      }
    return(
        <Wrapper menuOpen={menu === false}>
            <WrapperTop >
            <img  src={Logo} width={152} height={34} alt="logo" />
            <img src={Catalog} onClick={HandleClose} width={42} height={42} alt="" />
            </WrapperTop>
            <WrapperList>
            {DataLink.map((elem , index) =>
            <WrapperItem key={index}>
                                <NavLink to={elem.link} style={{textDecoration : "none"}}>
                                  <span>{t(elem.title)}</span>
                                </NavLink>
            </WrapperItem>

                    )}
            </WrapperList>
            <MenuFooter>
            <ul>
                        <li>
                            <button onClick={handleLang} value="uz">
                            <img src={UzFlag} width={24} height={24} style={{marginRight:"5px"}} alt="" />
                            Uz
                            </button>
                        </li>
                        <hr />
                        <li>
                            <button onClick={handleLang}  value="ru">
                            <img src={RuFlag} width={24} height={24} style={{marginRight:"5px"}} alt="" />
                            Ru
                            </button>
                        </li>
                        <hr />
                        <li>
                            <button onClick={handleLang}  value="en">                       
                                <img src={EnFlag} width={24} height={24} style={{marginRight:"5px"}} alt="" />
                            En</button>
                        </li>
                    </ul>
            </MenuFooter>

        </Wrapper>
    )
}
export default MediaMenu