import styled from "styled-components";

export const WrapperTop = styled.div`
    display: flex;
    justify-content: space-between;
    position: fixed;
    box-shadow: 0 4px 2px -2px  rgba(0, 0, 0, 0.05);
    max-width: 1310px;
    width: 100%;
    margin: 0 auto;
    padding: 0 10px;
    padding-top: 20px;
    padding-bottom: 20px;
    z-index: 1000;
    background-color: white;
    @media only screen and (max-width: 1341px)  {
        width: 90%;
    }
    @media only screen and (max-width: 920px)  {
        display: none;
    }
    select{
        background: #FAFAFA;
        border-radius: 40px;
        height: 30px;
        border: none;
        outline: none;
        width: 50px;
        cursor: pointer;
        padding: 5px;
    }
`

export const WrapperBottomCenter = styled.div`
    width: 38.6%;
    @media only screen and (max-width: 920px)  {
        width: 100%;
}
`
export const WrapperBottom = styled.div`
    padding-top: 110px;
    display: flex;
    justify-content: space-between;
    .overlay{
        display:none;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #0e0d0d;
        opacity: 0.7;
        z-index: 1;
        cursor: pointer;
    }
    @media only screen and (max-width: 920px)  {
    padding-top: 30px;
        img{
            display: none;
        }
}
    input{
        background: #FAFAFA;
        border: 0.5px solid #EBEBEB;
        box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.03);
        border-radius: 40px;
        padding-top: 20px;
        padding-bottom: 20px;
        padding-left: 20px;
        width: 96%;
        outline: transparent;
    }
    span:nth-child(1){
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 0.01em;
        color: #fff;
        text-decoration: none;
        background: #297D3B;
        border-radius: 40px;
        padding: 17px 23px;
        display: flex;
        align-items: center;
}
@media only screen and (max-width: 920px)  {
    span:nth-child(1){
        display: none;
    }
    input{
        width: 95%;
    }
}
`
export const WrapperBottomCenterIcons = styled.div`
    display: flex;
    flex-direction: column;
    text-align: center;
    @media only screen and (max-width: 920px)  {
        display: none;
    }
    div{
        display: flex;
        align-items: center;
        justify-content: center;

        img{
            background-color: #FAFAFA;
            border: 1px solid #FAFAFA;
            border-radius: 50%;
            padding: 10px 5px 0px;
        }
    }
    span{
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 0.01em;
        color: #242222;
    }

`
export const WrapperList = styled.ul`
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    width: 69%;
    list-style-type: none;
    align-items: center;
    @media only screen and (max-width: 1341px)  {
        width: 80%;
    }
    @media only screen and (max-width: 989px)  {
        width: 91%;
    }
`
export const WrapperItem = styled.li`
    span{
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 0.01em;
        color: #242222;
    }
`
export const WrapperNetWorker = styled.div`
    display: flex;
    justify-content: space-between;
    align-items:flex-end;
    width: 6%;
    @media only screen and (max-width: 1196px)  {
        width: 10%;
    }
    @media only screen and (max-width: 989px)  {
        display: none;
}
`
export const WrapperTopMedia = styled.div`
    display: none;
    @media only screen and (max-width: 920px)  {
        display: flex;
    justify-content: space-between;
    margin-top: 45px;
}
`
export const WrapperTopMediaLeft = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 83px;
`