import styled from "styled-components";

export const Wrapper = styled.div`
  margin-top: 3%;
  .headerImg {
    position: relative;
    width: 100%;
    height: 300px;
    #upload-photo {
      height: 0;
      width: 0;
    }

    #upload-photo-label {
      /* border: 1px solid #ffffff; */
      padding: 5px 30px;
      font-family: arial;
      font-size: 13px;
    }
    img {
      width: 100%;
      height: 100%;
      border-radius: 20px;
      @media screen and (max-width: 515px) {
        height: 250px;
      }
    }
    .addButton {
      position: absolute !important;
      top: 10%;
      left: 93.5%;
      border: 1px solid #0000001a;
      background: #0000001a;
      padding: 15px 15px 11px;
      font-size: 20px;
      color: white;
      border-radius: 50%;
      /* bottom: 0; */
      /* right: 0; */
      @media screen and (max-width: 1015px) {
        left: 91%;
      }
      @media screen and (max-width: 875px) {
        left: 89%;
      }
      @media screen and (max-width: 720px) {
        left: 85%;
      }
      @media screen and (max-width: 505px) {
        left: 80%;
      }
      @media screen and (max-width: 425px) {
        left: 75%;
      }
    }
  }
  .userInfor {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    margin-left: auto;
    position: relative;
    @media screen and (max-width: 805px) {
      width: 100%;
    }
    /* @media screen and (max-width: 605px) {
      img {
        position: absolute;
        top: -80%;
        left: 3%;
      }
    }
    @media screen and (max-width: 575px) {
      img {
        left: 32%;
      }
    } */
    .infor {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media screen and (max-width: 605px) {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        /* border: 1px solid black; */
        width: 100%;
        h2 {
          font-size: 20px;
        }
      }
      .img {
        border-radius: 50%;
        width: 200px;
        height: 200px;
        border: 20px solid white;
        overflow: hidden;
        position: absolute;
        cursor: pointer;
        img {
          border-radius: 50%;
          width: 200px;
          height: 200px;
        }
        .editDiv {
          text-align: center;
          button {
            position: absolute;
            z-index: -1;
            left: 0%;
            cursor: pointer;
            /* border-radius: 20px; */
            bottom: -20%;
            transition: all 0.65s;
            border: 1px solid white;
            color: white;
            border: 1px solid white;
            /* background-color: white; */
            width: 100%;
            height: 42px;

            .bx {
              font-size: 20px;
              margin: 0 auto;
            }
          }
        }
        @media screen and (max-width: 650px) {
          width: 150px;
          height: 150px;
          position: absolute;
          left: 0;
          top: -30%;
          img {
            width: 150px;
            height: 150px;
          }
        }
        @media screen and (max-width: 605px) {
          left: 30%;
        }
        @media screen and (max-width: 515px) {
          border: 15px solid white;
          width: 150px;
          height: 150px;
          img {
            width: 150px;
            height: 150px;
          }
          top: -90%;
        }
        @media screen and (max-width: 480px) {
          left: 27%;
        }
        @media screen and (max-width: 390px) {
          left: 24%;
        }
        @media screen and (max-width: 355px) {
          left: 20%;
        }
      }
      .img:hover > .editDiv > button {
        position: absolute;
        bottom: -1%;
        z-index: 100;
        background-color: rgba(0, 0, 0, 0.5);
        border: 1px solid #c0c0c0;
      }
      .texts {
        margin-left: 280px;
        @media screen and (max-width: 690px) {
          margin-left: 255px;
        }
        @media screen and (max-width: 650px) {
          margin-left: 210px;
        }
        @media screen and (max-width: 605px) {
          margin-left: 0;
        }
        @media screen and (max-width: 515px) {
          h2 {
            font-size: 18px;
          }
        }
        @media screen and (max-width: 515px) {
          margin-top: 40px;
        }
      }
    }
    .editBtn {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .iconBtn {
        display: none;
        @media screen and (max-width: 495px) {
          display: block;
        }
      }
      button {
        border: 1px solid #297d3b;
        background-color: #297d3b;
        padding: 10px 20px;
        border-radius: 20px;
        font-weight: 600;
        font-size: 17px;
        color: white;
        text-transform: capitalize;
        cursor: pointer;
        @media screen and (max-width: 575px) {
          font-size: 15px;
        }
        @media screen and (max-width: 495px) {
          display: none;
        }
      }
    }
  }
  .consulation {
    width: 75%;
    margin: 7% auto 0;
    @media screen and (max-width: 970px) {
      margin: 10% auto 0;
    }
    @media screen and (max-width: 805px) {
      width: 100%;
      margin: 15% auto 0 !important;
    }
  }
  form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 600px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    /* border: 1px solid black; */
    div {
      display: flex;
      flex-direction: column;
      /* border: 1px solid black; */
      width: 40%;
      @media screen and (max-width: 600px) {
        width: 100%;
      }
      input {
        border: 1px solid #bec8d0;
        padding: 15px 20px;
        border-radius: 30px;
        width: 90%;
      }
      select {
        border: 1px solid #bec8d0;
        padding: 15px 10px;
        border-radius: 30px;
        width: 100%;
      }
      label {
        margin: 20px 0 10px 0;
      }
      textarea {
        border: 1px solid #bec8d0;
        border-radius: 20px;
        resize: none;
        padding: 15px;
        width: 90%;
      }
    }
  }
`;

export const Put = styled.div`
  padding-right: 5%;
  .bx {
    color: white;
    background-color: #297d3b;
    padding: 10px;
    border: 1px solid #297d3b;
    border-radius: 50%;
    cursor: pointer;
  }
`;
