import styled from "styled-components";

import AlurraImg from "../../../assets/images/client/addProect/Alurra.png";

export const Wrapper = styled.div`
  max-width: 1310px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
  @media screen and (max-width: 640px) {
    padding: 0;
  }
`;

export const Inputs = styled.div`
  width: 50%;
  label{
    margin: 3.5% 0 0 0;
    padding: 0;
    font-size: 25px;
}
  .investment {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 98%;

    input {
      width: 45%;
    }
  }
  @media screen and (max-width: 640px) {
    width: 90%;
  }
  form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @media screen and (max-width: 640px) {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
    }

    input {
      width: 93.5%;
      margin-top: 3%;
      padding: 4% 2%;
      background-color: #f5f5f5;
      border: 1px solid #f5f5f5;
      border-radius: 10px;
      font-family: "Open Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 27px;
      letter-spacing: 0.01em;
      color: #808080;

      @media screen and (max-width: 640px) {
        width: 95%;
        padding: 6% 2%;
        font-family: "Open Sans";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 0.01em;
        color: #808080;
      }
    }
    select {
      width: 97.5%;
      margin-top: 3%;
      padding: 4% 2%;
      background-color: #f5f5f5;
      border: 1px solid #f5f5f5;
      border-radius: 10px;
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 27px;
      letter-spacing: 0.01em;
      color: #808080;
      @media screen and (max-width: 640px) {
        width: 100%;
        padding: 6% 2%;
        font-family: "Open Sans";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 0.01em;
        color: #808080;
      }
    }
    textarea {
      width: 93.5%;
      margin-top: 3%;
      padding: 4% 2%;
      background-color: #f5f5f5;
      border: 1px solid #f5f5f5;
      border-radius: 10px;
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      resize: none;
      line-height: 27px;
      letter-spacing: 0.01em;
      color: #808080;
      @media screen and (max-width: 640px) {
        width: 95%;
        padding: 6% 2%;
        font-family: "Open Sans";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 0.01em;
        color: #808080;
      }
    }
    button {
      padding: 2% 3%;
      margin-top: 2%;
      border-radius: 10px;
      background-color: #297d3b;
      color: white;
      border: 2px solid #297d3b;
      font-weight: 600;
      font-size: 100%;
      transition: 0.65s;
      cursor: pointer;
      @media screen and (max-width: 640px) {
        margin-top: 3%;
        padding: 6% 15%;
      }
      &:hover {
        background-color: white;
        color: #297d3b;
      }
    }
  }
`;

export const Title = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 3%;
  @media screen and (max-width: 640px) {
    text-align: center;
  }
`;

export const AddImg = styled.div`
  background-image: url(${AlurraImg});
  background-size: cover;
  width: 50%;
  img {
    width: 70%;
    display: flex;
    margin-left: auto;
    margin-top: 30px;
  }
  @media screen and (max-width: 640px) {
    display: none;
  }
`;

export const Heading = styled.div`
  border-bottom: 1px solid #808080;
  @media screen and (max-width: 497px) {
    border-bottom: none;
  }

  h1 {
    font-family: "Open Sans", sans-serif;

    @media screen and (max-width: 497px) {
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 27px;
      letter-spacing: 0.01em;
    }
  }
`;
