import { display } from "@mui/system";
import React, { useState } from "react";
import { useEffect } from "react";
import { useRef } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import Cookies from "universal-cookie";
import {
  GetProdfile,
  ProfilePost,
  PutProfile,
  UploadImage,
  UploadImage2,
} from "../../../store/client/profile";
import { Wrapper, Put } from "./styled-index";
import backimg from "../../../assets/images/client/profile/back.png";
import { GetUserCategory } from "../../../store/client/userCateg";

function ProfilePage() {
  const cookies = new Cookies();
  const user = cookies.get("userCreate");
  const userid = cookies.get("userID");
  const { t, i18n } = useTranslation();
  const inputFileRef = useRef(null);
  const inputFileRef2 = useRef(null);
  const dispatch = useDispatch();
  const nameCompany = useRef();
  const number = useRef();
  const email = useRef();
  const consul = useRef();
  const county = useRef();
  const telegram = useRef();
  const password = useRef();
  const insta = useRef();
  const expi = useRef();
  const desc = useRef();
  const img = useSelector((state) => state.profile.uploadProfile.data);
  const img2 = useSelector((state) => state.profile.uploadProfile2.data2);
  const [dis, setDis] = useState(false);
  // const [btn, SetBtn] = useState(false);
  const btn = cookies.get("userID");
  const [categoryId, setCategory] = useState();
  const Loading = useSelector((state) => state.profile?.uploadProfile?.Loading);
  const Loading2 = useSelector(
    (state) => state.profile?.uploadProfile2?.Loading
  );

  useEffect(() => {
    dispatch(GetUserCategory());
  }, []);
  const Datass = useSelector(
    (state) => state?.userCateg?.getUserCategory?.Data
  );

  const onFilechange = (e) => {
    dispatch(UploadImage(e));
  };
  const onBtnClick = (e) => {
    e.preventDefault();
    inputFileRef.current.click();
  };
  const onFilechange2 = (e) => {
    dispatch(UploadImage2(e));
  };
  const onBtnClick2 = (e) => {
    e.preventDefault();
    inputFileRef2.current.click();
  };
  const HandelCilck = async (e) => {
    e.preventDefault();
    const body = {
      company_name: nameCompany.current.value,
      phone_number: number.current.value,
      email: email.current.value,
      categoryId: categoryId,
      country: county.current.value,
      tg_username: telegram.current.value,
      inst_username: insta.current.value,
      experience: expi.current.value,
      comments: desc.current.value,
      company_img: img,
      project_img: img2,
      password: password.current.value,
    };
    await dispatch(ProfilePost(body));
    setDis(true);
  };
  const id = userid;
  const HandelPut = async (e) => {
    e.preventDefault();
    const body = {
      company_name: nameCompany.current.value,
      phone_number: number.current.value,
      email: email.current.value,
      categoryId: categoryId,
      country: county.current.value,
      tg_username: telegram.current.value,
      inst_username: insta.current.value,
      experience: expi.current.value,
      comments: desc.current.value,
      company_img: img,
      project_img: img2,
    };
    await dispatch(PutProfile({ body, id: id }));
    dispatch(GetProdfile());
    setDis(true);
  };
  const data = useSelector((state) => state.profile.getProf?.Data);
  useEffect(() => {
    dispatch(GetProdfile());
  }, []);
  const Date = data?.data?.find((elem, index) => elem.id == userid);
  return (
    <Wrapper>
      <div className="head">
        <div className="headerImg">
          {Date ? (
            <>
              <img src={Date.company_img} alt="photo" />
            </>
          ) : (
            <>
              <img src={backimg} alt="photo" />
            </>
          )}
          <form className="some-container">
            <input
              type="file"
              style={{ display: "none" }}
              ref={inputFileRef}
              onChange={onFilechange}
            />
            <button className="addButton" onClick={onBtnClick}>
              {Loading ? (
                <>
                  <i class="bx bx-loader-circle bx-spin"></i>
                </>
              ) : (
                <>
                  <i class="bx bxs-camera-plus"></i>
                </>
              )}
            </button>
          </form>
        </div>
        <div className="userInfor">
          <div className="infor">
            <div className="img">
              {Date ? (
                <>
                  <img src={Date.project_img} alt="photo" />
                </>
              ) : (
                <>
                  <img
                    src="https://www.station-drivers.com/media/kunena/avatars/resized/size200/gallery/avatar_defaut.png"
                    alt="hpto"
                  />
                </>
              )}
              <div className="editDiv">
                <input
                  type="file"
                  style={{ display: "none" }}
                  ref={inputFileRef2}
                  onChange={onFilechange2}
                />
                <button className="editBtn" onClick={onBtnClick2}>
                  {Loading2 ? (
                    <>
                      <i class="bx bx-loader-circle bx-spin"></i>
                    </>
                  ) : (
                    <>
                      <i class="bx bx-camera"></i>
                    </>
                  )}
                </button>
              </div>
            </div>
            {Date ? (
              <>
                <div className="texts">
                  <h2>{Date.company_name}</h2>
                </div>
              </>
            ) : (
              <div className="texts">
                <h2>UserName</h2>
              </div>
            )}
          </div>
          <div className="editBtn">
            <Put>
              {user == "true" ? (
                <i
                  onClick={() => {
                    setDis(false);
                    // SetBtn(true);
                  }}
                  class="bx bxs-edit-alt"
                ></i>
              ) : null}
            </Put>
            {btn ? (
              <>
                <button onClick={HandelPut}>{t("Profile.20")}</button>
              </>
            ) : (
              <button onClick={HandelCilck} className="textBtn">
                {t("Profile.0")}
              </button>
            )}
            <button onClick={HandelCilck} className="iconBtn">
              <i class="bx bxs-send"></i>
            </button>
          </div>
        </div>
      </div>
      <div className="consulation">
        <h2>{t("Profile.1")}</h2>
        {Date ? (
          <>
            <form>
              <div>
                <label>{t("Profile.2")}</label>
                <input
                  type="text"
                  ref={nameCompany}
                  placeholder={Date.company_name}
                  disabled={dis}
                />

                <label>{t("Profile.4")}</label>
                <input
                  type="number"
                  ref={number}
                  placeholder={Date.phone_number}
                  disabled={dis}
                />

                <label>{t("Profile.6")}</label>
                <input
                  type="email"
                  ref={email}
                  placeholder={Date.email}
                  disabled={dis}
                />
                {/* 
                <label>{t("Profile.8")}</label>
                <input
                  type="text"
                  ref={consul}
                  placeholder={Date.project_direction}
                  disabled={dis}
                /> */}
                <label>{t("Profile.8")}</label>
                <select onChange={(e) => setCategory(e.target.value)}>
                  <option value="">{Date.project_direction}</option>
                  {Datass?.map((elem, index) => (
                    <option value={elem.id}>{elem.title}</option>
                  ))}
                </select>

                <label>{t("Profile.10")}</label>
                <input
                  type="text"
                  ref={county}
                  placeholder={Date.country}
                  disabled={dis}
                />
              </div>
              <div>
                <label>{t("Profile.12")}</label>
                <input
                  type="text"
                  ref={telegram}
                  placeholder={Date.tg_username}
                  disabled={dis}
                />

                <label>{t("Profile.14")}</label>
                <input
                  type="text"
                  ref={insta}
                  placeholder={Date.inst_username}
                  disabled={dis}
                />

                <label>{t("Profile.16")}</label>
                <input
                  type="number"
                  ref={expi}
                  placeholder={Date.experience}
                  disabled={dis}
                />

                <label>{t("Profile.18")}</label>
                <textarea
                  rows="7"
                  ref={desc}
                  disabled={dis}
                  placeholder={Date.comments}
                ></textarea>
              </div>
            </form>
          </>
        ) : (
          <>
            <form>
              <div>
                <label>{t("Profile.2")}</label>
                <input
                  type="text"
                  ref={nameCompany}
                  placeholder={t("Profile.3")}
                  disabled={dis}
                />

                <label>{t("Profile.4")}</label>
                <input
                  type="number"
                  ref={number}
                  placeholder={t("Profile.5")}
                  disabled={dis}
                />

                <label>{t("Profile.6")}</label>
                <input
                  type="email"
                  ref={email}
                  placeholder={t("Profile.7")}
                  disabled={dis}
                />

                {/* <label>{t("Profile.8")}</label>
                <input
                  type="text"
                  ref={consul}
                  placeholder={t("Profile.9")}
                  disabled={dis}
                /> */}
                <label>{t("Profile.8")}</label>
                <select onChange={(e) => setCategory(e.target.value)}>
                  <option value="">{t("Profile.8")}</option>
                  {Datass?.map((elem, index) => (
                    <option value={elem.id}>{elem.title}</option>
                  ))}
                </select>

                <label>{t("Profile.10")}</label>
                <input
                  type="text"
                  ref={county}
                  placeholder={t("Profile.11")}
                  disabled={dis}
                />

                <label>{t("Profile.21")}</label>
                <input
                  type="password"
                  ref={password}
                  placeholder={t("Profile.21")}
                  disabled={dis}
                />
              </div>
              <div>
                <label>{t("Profile.12")}</label>
                <input
                  type="text"
                  ref={telegram}
                  placeholder={t("Profile.13")}
                  disabled={dis}
                />

                <label>{t("Profile.14")}</label>
                <input
                  type="text"
                  ref={insta}
                  placeholder={t("Profile.15")}
                  disabled={dis}
                />

                <label>{t("Profile.16")}</label>
                <input
                  type="number"
                  ref={expi}
                  placeholder={t("Profile.17")}
                  disabled={dis}
                />

                <label>{t("Profile.18")}</label>
                <textarea
                  rows="14"
                  ref={desc}
                  disabled={dis}
                  placeholder={t("Profile.19")}
                ></textarea>
              </div>
            </form>
          </>
        )}
      </div>
    </Wrapper>
  );
}

export default ProfilePage;
