import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import Cookies from "universal-cookie";
const cookies = new Cookies();

export const SuLogin = createAsyncThunk(
  "user/Slogin",
  async (userData, { rejectWithValue, dispatch }) => {
    try {
      let response = await axios.post(
        "https://startap.supersiteuz.com/superUser/login",
        userData
      );
      console.log(response);
      if (response.data.message === "User found") {
        cookies.set("userCreate", true);
        window.localStorage.setItem("Diss", true);
        cookies.set("userID", response?.data?.data?.id);
        cookies.set("autToken", response?.data?.token);
        window.location = "/";
      }
      if (!response.status) {
        throw new Error("Internal Server Error");
      }
      return { ...userData };
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);

const initialState = {
  loading: false,
  email: null,
  password: null,
  error: "",
};

const SuperLoginSlice = createSlice({
  name: "Slogin",
  initialState,
  extraReducers: (builder) => {
    builder.addCase(SuLogin.pending, (state) => {
      state.loading = true;
    });
    builder.addCase(SuLogin.fulfilled, (state, action) => {
      state.loading = false;
      state.users = action.payload;
      state.error = "";
    });
    builder.addCase(SuLogin.rejected, (state, action) => {
      state.loading = false;
      state.users = [];
      state.error = action.error.message;
    });
  },
});

export default SuperLoginSlice.reducer;
