import styled from "styled-components";

export const Wrapper = styled.div`
  font-family: "Open sans";
  margin-top: 3%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100% !important;
  h3,
  p,
  button,
  h4 {
    margin-left: 5%;
  }
`;

export const LookButton = styled.div`
  text-align: center;
  button {
    background-color: #fafafa;
    color: black;
    border: 1px solid #fafafa;
    padding: 10px 20px;
    border: 1px solid #fafafa;
    border-radius: 50px;
    margin-top: 5%;
    cursor: pointer;
    transition: 0.65s;
    border: 1px solid #297d3b;
    &:hover {
      background: #297d3b;
      color: white;
    }
  }
`;

export const DivList = styled.div`
  background: #fafafa;
  width: 299px;
  border-radius: 10px;
  margin-top: 3%;
  padding-bottom: 1.5%;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
  img {
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  button {
    padding: 10px 30px;
    border-radius: 50px;
    border: 1px solid #297d3b;
    background: #297d3b;
    color: white;
    cursor: pointer;
    transition: 0.65s;
    &:hover {
      background-color: #fafafa;
      color: black;
    }
  }

  @media screen and (max-width: 768px) {
    width: 45%;
    padding: 0;
    h3 {
      text-align: center;
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 27px;
      letter-spacing: 0.01em;
    }
    p {
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 19px;

      letter-spacing: 0.01em;

      margin: 2% 0% 0 5%;
    }
    h4 {
      margin: 2% 0 2% 4%;
    }
    button {
      border-radius: 0px;
      width: 100%;
      margin: 3% 0 0 0;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;
      text-align: center;
    }
  }
`;
