import styled from "styled-components"
import footerBackimg from "../../../assets/images/client/footer/footerBackimg.png"

export const Wrapper = styled.div`
background-image: url(${footerBackimg});
background-size: 100%;
background-repeat: no-repeat;
background-position-y: -30px;
font-family: 'Open Sans';
@media screen and (max-width: 921px){
    background-image: none;
    background-color: #FAFAFA;
}

`

export const Title = styled.div`
display: flex;
justify-content: space-between;
margin-top: 5%;
@media screen and (max-width: 637px) {
    width: 95%;
    ul li a {
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.03em;
    }
}

ul {
    list-style: none;
    width: auto;
    margin: 0;
    padding: 0;
}
ul li {
    padding-bottom: 5%;
}
ul li i {
    color: #297D3B;
    font-size: 25px;
    margin: 0 3% 0 0;
    cursor: pointer;
}

ul li h3 {
    padding-bottom: 3%;
}
ul li a {
    color: #242222;
    text-decoration: none;
}
h2 {
    letter-spacing: 1px;    
    font-size: 25px;
    span{
        color: #297D3B;
    }
}
`

export const Heading = styled.div`
display: flex;
justify-content: space-between;
width: 37%;
@media screen and (max-width: 921px) {
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
@media screen and (max-width: 709px) {
    width: 30%;
    display: flex;
    flex-wrap: wrap;
}
@media screen and (max-width: 637px) {
    width: 30%;
    padding: 10px;
}
ul li {
    padding-bottom: 8%;
}
`

export const Ul = styled.ul`
li {
    padding-bottom: 8% !important;
}
@media screen and (max-width: 921px) {
    display: none;
}
`

export const P = styled.p`
padding: 2% 0;
@media screen and (max-width: 709px) {
    text-align: center;
}
`