import styled from "styled-components";

export const Wrapper = styled.div`
  .cols {
    background-color: #fafafa;
    margin-top: 7%;
    border-radius: 20px;
    @media screen and (max-width: 767px) {
      width: 70%;
      margin: 7% auto 0;
    }
    @media screen and (max-width: 545px) {
      width: 100%;
      margin: 7% auto 0;
    }
    h2 {
      font-size: 35px;
    }
    img {
      width: 100%;
      height: 300px;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
    }
    .texts {
      padding: 0 20px 20px;
      button {
        background: #297d3b;
        padding: 13px 30px;
        color: white;
        letter-spacing: 1px;
        font-size: 13px;
        border: 1px solid #297d3b;
        border-radius: 20px;
        cursor: pointer;
      }
    }
  }
  .moreBtn {
    margin-top  : 3%;
    .btn {
      display: flex;
      justify-content: center;
      align-items: center;
      button {
        background: #EBEBEB;
        padding: 13px 30px;
        cursor: pointer;
        color: black;
        cursor: pointer;
        letter-spacing: 1px;
        font-size: 13px;
        border: 1px solid #EBEBEB;
        border-radius: 20px;
        transition: .65s;
        &:hover{
        background-color: #297d3b;
        color: white;
        }
      }
    }
  }
`;

export const  Header = styled.div`
h2 {
font-size: 35px;
font-weight: 600;
margin: 3% 0 0 0; 
}
p{
margin: 1% 0 3% 0;
letter-spacing: 1px;
font-size: 18px;
}
`