import { useTranslation } from "react-i18next";
import {
  Wrapper,
  WrapperLeft,
  First,
  WrapperRight,
  Inform,
  Button,
  Bigdiv,
  Hr,
  Border,
} from "./styled-index";
import { WrapperContainer } from "../../../App-styled";
import CardComponent from "../home/new-project/card";
import Picture from "../../../assets/images/client/newProject/team.png";
import ModalLinks from "./modal";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { GetCategory, GetProject } from "../../../store/client/addProect";

function MoreComponent() {
  const { t, i18next } = useTranslation();
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetCategory());
    dispatch(GetProject());
  }, []);
  const datas = useSelector(
    (state) => state?.addProject?.getProject?.Data?.data
  );
  const MoreId = window.localStorage.getItem("moreIDD");
  return (
    <>
      <WrapperContainer>
        {datas?.map((elem, index) =>
          elem.id == MoreId ? (
            <>
              <Wrapper>
                <WrapperLeft>
                  <First>
                    <h1>{elem.company_name}</h1>
                    <p>{t("More.1")}</p>
                  </First>
                  <div className="mobile-div">
                    <img width={150} height={134} src={Picture} alt="" />
                    <Inform>
                      <h1>Основная </h1>
                      <p>Возраст бизнеса - {elem.business_age} лет</p>
                      <p>Количество сотрудников - {elem.employees_number}</p>
                      <p>Действующий бизнес</p>
                      <p>Окупаемость - 2 года</p>
                      <p>Причина продажи - что то</p>
                    </Inform>
                  </div>
                  <img className="img-desktoop" src={Picture} alt="" />
                  <h2>{t("More.2")}</h2>
                  <p>{t("More.3")}</p>
                  <h3 className="subtext">1.200.100 - 3.300.200 $</h3>
                  <p>{t("More.4")}</p>
                  <h3 className="subtext">{t("More.5")}</h3>
                  <p>{t("More.6")}</p>
                  <h3 className="subtext">1.200.100 - 1.800.200 $</h3>
                  <p>{t("More.7")}</p>
                  <h3 className="subtext">{t("More.8")}</h3>
                </WrapperLeft>
                <WrapperRight>
                  <Button>
                    <button onClick={handleOpen}>{t("More.9")}</button>
                    {/* <button>{t("More.10")}</button> */}
                  </Button>
                  <div className="desktoop">
                    <h1 style={{ marginTop: "70px" }}>{t("More.11")}</h1>
                    <p>{t("More.12")}</p>
                    <p>{t("More.13")}</p>
                    <p>{t("More.14")}</p>
                    <p>{t("More.15")}</p>
                    <p style={{ marginBottom: "100px" }}>{t("More.16")}</p>
                  </div>
                  <h2>{t("More.17")}</h2>
                  <p style={{ width: "70%" }}>{t("More.18")}</p>
                </WrapperRight>
              </Wrapper>
            </>
          ) : null
        )}
        <ModalLinks open={open} setOpen={setOpen} handleClose={handleClose} />
        <CardComponent />
      </WrapperContainer>
    </>
  );
}
export default MoreComponent;
