import { useTranslation } from "react-i18next";
import { WrapperContainer } from "../../../App-styled";
import { Wrapper, H2, WrapperDiv } from "./styled-index";
import { useState, useEffect } from "react";
import { Col, Row } from "react-grid-system";
import { useDispatch, useSelector } from "react-redux";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import { GetCategory, GetProject } from "../../../store/client/addProect";
import "../home/newProject/styles.css";
import News from "../news";
import { NavLink } from "react-router-dom";

function SearchComp() {
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetCategory());
    dispatch(GetProject());
  }, []);
  const Categorys = useSelector(
    (state) => state?.addProject?.getCategory?.Data?.data
  );
  const datas = useSelector(
    (state) => state?.addProject?.getProject?.Data?.data
  );

  const [values, setValue] = useState("");
  const [val, setVal] = useState(0);
  console.log(values);
  const handleChange = (event, newValue) => {
    setValue(event.target.id);
    setVal(newValue);
  };
  return (
    <>
      <WrapperDiv>
        <WrapperContainer>
          <h2>Ищу хороший проект</h2>
          <Wrapper>
            <Tabs
              value={val}
              onChange={handleChange}
              aria-label="basic tabs example"
            >
              {Categorys?.map((elem, index) => (
                <Tab key={index} id={elem.title} label={elem.title} />
              ))}
            </Tabs>
            <Row style={{ margin: 0 }}>
              {values == "" ? (
                <>
                  {datas?.map((elem) => (
                    <Col lg={4} md={6} sm={12}>
                      <div className="cols">
                        <img src={elem.img} alt="photo" />
                        <div className="texts">
                          <h3>{elem?.categoryId?.title}</h3>
                          <p>{elem.company_name}</p>
                          <p>Возраст бизнеса - {elem.business_age} лет</p>
                          <p>
                            Количество сотрудников - {elem.employees_number}
                          </p>
                          <p>Окупаемость - {elem.payback} года</p>
                          <p>Причина продажи - {elem.reason_for_sale}</p>
                          <NavLink
                            to={"/more"}
                            onClick={() =>
                              window.localStorage.setItem("moreIDD", elem.id)
                            }
                          >
                            <button>{t("Search.3")}</button>
                          </NavLink>
                        </div>
                      </div>
                    </Col>
                  ))}
                </>
              ) : (
                <>
                  {datas?.map((elem) =>
                    elem?.categoryId?.title == values ? (
                      <Col lg={4} md={6} sm={12}>
                        <div className="cols">
                          <img src={elem.img} alt="photo" />
                          <div className="texts">
                            <h3>{elem?.categoryId?.title}</h3>
                            <p>{elem.company_name}</p>
                            <p>Возраст бизнеса - {elem.business_age} лет</p>
                            <p>
                              Количество сотрудников - {elem.employees_number}
                            </p>
                            <p>Окупаемость - {elem.payback} года</p>
                            <p>Причина продажи - {elem.reason_for_sale}</p>
                            <button>{t("Search.3")}</button>
                          </div>
                        </div>
                      </Col>
                    ) : null
                  )}
                </>
              )}
            </Row>
          </Wrapper>
          <News />
        </WrapperContainer>
      </WrapperDiv>
    </>
  );
}
export default SearchComp;
