import styled from "styled-components";


export const UserInformation = styled.div`
h2 {
    font-size: 40px;
}
`
export const Users = styled.div`
display: flex;
align-items: center;
justify-content: space-between;
width: 40%;
img {
    width: 60%;
    @media screen and (max-width: 436px) {
    width: 50%;
    }
}

@media screen and (max-width: 1300px) {
    width: 45%;
    }
    @media screen and (max-width: 1160px) {
    width: 50%;
    }
    @media screen and (max-width: 900px) {
    width: 60%;
    }
    @media screen and (max-width: 768px) {
    width: 70%;
    }
    @media screen and (max-width: 668px) {
    width: 100%;
    }

`

export const UserTexts = styled.div`
@media screen and (max-width: 668px) {
    p{
        margin: 2% 0;
    }
    }
`
export const ChatDiv = styled.div`
background: #F5F5F5;
width: 75%;
padding: 10px 20px;
border-radius: 20px;
margin-top: 10%;
@media screen and (max-width: 1125px) {
    width: 95%;
    }
`

export const ChatUser = styled.div`

@media screen and (max-width: 1341px) {
    width: 45%;
}
@media screen and (max-width: 1275px) {
    width: 50%;
    }

    @media screen and (max-width: 895px) {
    width: 55%;
    }
    @media screen and (max-width: 820px) {
    width: 60%;
    }
    @media screen and (max-width: 743px) {
    width: 65%;
    }
    @media screen and (max-width: 681px) {
    width: 70%;
    }
    @media screen and (max-width: 635px) {
    width: 80%;
    }
    @media screen and (max-width: 556px) {
    width:85%;
    }
width: 43%;
div{
    display: flex;
align-items: center;
justify-content: space-between;

p{
    border: 1px solid #465666;
    color: white;
    background-color: #465666;
    padding: 5px 20px;
    width: auto;
    border-radius: 50px;
    margin: 0;
    @media screen and (max-width: 1341px) {
    padding: 5px 10px;
    }
    @media screen and (max-width: 1260px) {
    padding: 5px 7px;
    }
    @media screen and (max-width: 1135px) {
    padding: 5px 5px;
    }
    @media screen and (max-width: 556px) {
        border-radius: 5px;
    }


}
}
`
export const P = styled.p`
 text-align: end;
    margin: 0;
`

export const Messeges = styled.div`
form {
    margin-top: 25%;
    div{
        display: flex;
        align-items: start;
        justify-content: space-between;
        input {
            width: 88%;
            outline: none;
            border-radius: 10px;
            border: none;
            padding: 10px 20px 50px 20px;
        }
    }
    button {
        margin-top: 5%;
        border: 1px solid #297D3B;
        background-color: #297D3B;
        color: white;
        font-weight: 600;
        padding:  10px 20px;
        border-radius: 50px;
        cursor: pointer;
        transition: .65s;

        &:hover {
            background-color: #fafafa;
            color: #297D3B;
        }
        @media screen and (max-width: 556px) {
        display: none;
    }
    }
}
`

export const Messeg = styled.div`
display: flex;
align-items: flex-end;
flex-direction: column;
justify-content: flex-end;
p{
font-weight: 500;
font-size: 14px; 
}

p:nth-child(1) {
    border: 1px solid #297D3B;
    padding: 5px 20px;
    border-radius: 50px;
    color: white;
    background-color: #297D3B;
    @media screen and (max-width: 556px) {
        border-radius: 5px;
    }
}

p:nth-child(2) {
    margin: 0; 
}
`
