import styled from "styled-components";

export const WrapperContainer = styled.div`

`
export const Wrapper = styled.div`
display: flex;
padding-top: 30px;
justify-content: space-between;
@media only screen and (max-width : 920px) {
   flex-direction: column;
}
`
export const WrapperLeft = styled.div`
width: 35%;
.mobile-div{
   display: none;
   justify-content: space-between;
}
.subtext{
margin: 0;
padding: 0;
}
@media only screen and (max-width : 920px) {
   width: 100%;
   .mobile-div{
   display: flex;
   img{
      padding-top: 20px;
      @media only screen and (max-width :400px){
      width: 140px;
      height: 130px;
   }
   }
}
}
.img-desktoop{
   width: 500px;
   height: 300.55px;
   @media only screen and (max-width :920px){
      display: none;
   }
}
h2{
   font-family: 'Open Sans';
   font-style: normal;
   font-weight: 400;
   font-size: 35px;
   line-height: 48px;
   letter-spacing: 0.01em;
   color: #242222;
   @media only screen and (max-width :500px){
      font-family: 'Open Sans';
      font-style: normal;
      font-weight: 600;
      font-size: 25px;
      line-height: 23px;
      letter-spacing: 0.01em;
      color: #242222;
   }
}
p{
   font-family: 'Open Sans';
   font-style: normal;
   font-weight: 400;
   font-size: 20px;
   line-height: 27px;
   letter-spacing: 0.01em;
   color: #242222;
   @media only screen and (max-width :500px){
      font-family: 'Open Sans';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 0.01em;
      color: #242222;
   }
}

h3{
   font-family: 'Open Sans';
   font-style: normal;
   font-weight: 600;
   font-size: 35px;
   line-height: 48px;
   letter-spacing: 0.01em;
   color: #297D3B;
   @media only screen and (max-width :500px){
      font-family: 'Open Sans';
      font-style: normal;
      font-weight: 600;
      font-size: 25px;
      line-height: 34px;
      letter-spacing: 0.01em;
      color: #297D3B;
   }
}
`
