export const Button = styled.div`
   display: flex;
   flex-direction: column;
   align-items: flex-end;
   button{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 0.01em;
    width: 200px;
   height: 55px;
   background: #EBEBEB;
   border-radius: 55px;
   border: none;
   transition: .65s;
   margin-bottom: 10px;
   @media only screen and (max-width :920px){
  width: 100%;
 }
}
button:hover{
    background: #297D3B;
    border-radius: 55px;
    color: white;
}
@media only screen and (max-width :920px){
  width: 100%;
 }
`