import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
import Cookies from "universal-cookie";
const cookies = new Cookies();
export const SinUpUsers = createAsyncThunk(
  "user/SingUpUsers",
  async (userData, { rejectWithValue, dispatch }) => {
    try {
      let response = await axios.post(
        "https://startap.supersiteuz.com/users/register",
        userData
      );
      console.log(response.data?.data?.user_id);
      if (response.data?.message === "User created") {
        cookies.set("autToken", response.data.token);
        cookies.set("userId", response.data?.data?.user_id);
        window.location = "/";
      }
      if (!response.status) {
        throw new Error("Internal Server Error");
      }
      return { ...userData };
    } catch (error) {
      console.log(error);
      return rejectWithValue(error.message);
    }
  }
);
const initialState = {
  loading: false,
  username: null,
  email: null,
  password: null,
  repeatPassword: null,
  phone_number: null,
  error: "",
};
const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    // setUser(state , action){
    //     state.username = action.payload.username;
    //     state.email = action.payload.email;
    //     state.password = action.payload.password;
    //     state.repeat_password = action.payload.repeat_password;
    //     state.phone = action.payload.phone;
    // },
    // removeUser(state){
    //     state.email = null;
    //     state.password = null;
    //     state.id = null;
    //     state.token= null;
    // }
  },
  extraReducers: (builder) => {
    builder.addCase(SinUpUsers.pending, (state) => {
      state.loading = true;
    });
    builder.addCase(SinUpUsers.fulfilled, (state, action) => {
      state.loading = false;
      state.users = action.payload;
      state.error = "";
    });
    builder.addCase(SinUpUsers.rejected, (state, action) => {
      state.loading = false;
      state.users = [];
      state.error = action.error.message;
    });
  },
});
export default userSlice.reducer;
