import { Buttons, Button, Li, Heading } from "./styled-index";
import { WrapperContainer } from "../../../App-styled";
import ConsulList from "./consultationList/index";
import { useTranslation } from "react-i18next";
import { Tabs } from "antd";
import News from "../news";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from "react";
import { GetUserCategory } from "../../../store/client/userCateg";
function Consultation() {
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  const [id, setId] = useState();
  useEffect(() => {
    dispatch(GetUserCategory());
  }, []);
  const Data = useSelector((state) => state?.userCateg?.getUserCategory?.Data);
  const items = Data?.map((elem, index) => {
    const id = elem.id;
    return {
      label: `${elem.title}`,
      key: id,
      children: `${elem.id}`,
    };
  });
  return (
    <>
      <WrapperContainer>
        <div>
          <Heading>{t("Consul.0")}</Heading>
          <Tabs defaultActiveKey="1" items={items} />
        </div>
        <ConsulList ids={id} />
        <News />
      </WrapperContainer>
    </>
  );
}

export default Consultation;
