import { NavLink } from "react-router-dom";
import { WrapperContainer } from "../../../App-styled";
import { Wrapper } from "./styled-indxe";

function MediaLoginComp() {
  return (
    <WrapperContainer>
      <Wrapper>
        <div className="buttons">
          <NavLink
            className="navlink"
            to={"/singin"}
            style={{ textDecoration: "none" }}
          >
            <button className="user">
              <i class="bx bx-user-circle"></i>
              User
              <span>
                <i class="bx bx-chevron-right bx-fade-left"></i>
              </span>
            </button>
          </NavLink>
          <NavLink to={"/superlogin"} style={{ textDecoration: "none" }}>
            <button className="super">
              <i class="bx bx-user-circle"></i>
              SuperUser
              <span>
                <i class="bx bx-chevron-right bx-fade-left"></i>
              </span>
            </button>
          </NavLink>
        </div>
      </Wrapper>
    </WrapperContainer>
  );
}

export default MediaLoginComp;
