import {
  Wrapper,
  Inputs,
  Title,
  AddImg,
  Heading,
} from "../../../page/client/addProect/styled-index";
import addImg from "../../../assets/images/client/addProect/addimg.png";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { GetCategory, Upload , ProjectPost } from "../../../store/client/addProect";
import { useRef } from "react";
import { useState } from "react";
import Cookies from "universal-cookie";
import "./styles.css";

function AddProect() {
  const cookies = new Cookies();
  const [id, setId] = useState("");
  const nameCompany = useRef("");
  const age = useRef();
  const number = useRef("");
  const payback = useRef("");
  const dec = useRef();
  const web = useRef("");
  const invDo = useRef();
  const invpos = useRef();
  const revan = useRef();
  const LumpDo = useRef();
  const lumpPos = useRef();
  const profit = useRef("");
  const Userid = cookies.get("userId");
  const file = useSelector((state) => state?.addProject?.uploadProject?.data);
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetCategory());
  }, []);
  const Categories = useSelector(
    (state) => state?.addProject?.getCategory?.Data?.data
  );
  const UploadProject = (e) => {
    dispatch(Upload(e));
  };

  const HandelPost = async (e) => {
    e.preventDefault();
    const body = {
      business_age: age.current.value,
      company_name: nameCompany.current.value,
      employees_number: number.current.value,
      img: file,
      payback: payback.current.value,
      reason_for_sale: dec.current.value,
      website: web.current.value,
      user_id: Userid,
      investment_before: invDo.current.value,
      investment_after: invpos.current.value,
      revenue: revan.current.value,
      lump_cum_before: LumpDo.current.value,
      lump_cum_after: lumpPos.current.value,
      possible: profit.current.value,
      categoryId: id,
      user_id : Userid
    };
    await dispatch(ProjectPost(body))
  };

  return (
    <>
      <Wrapper>
        <Title>
          <Inputs>
            <Heading>
              <h1>{t("AddProect.0")}</h1>
            </Heading>
            <form>
              <select onChange={(e) => setId(e.target.value)}>
                {Categories?.map((elem, index) => (
                  <option key={index} value={elem.id}>
                    {elem.title}
                  </option>
                ))}
              </select>
              <input
                ref={nameCompany}
                type="text"
                placeholder={t("AddProect.2")}
              />
              <input ref={age} type="text" placeholder={t("AddProect.3")} />
              <input ref={number} type="text" placeholder={t("AddProect.4")} />
              <input ref={payback} type="text" placeholder={t("AddProect.5")} />
              <textarea
                cols="30"
                rows="5"
                placeholder={t("AddProect.6")}
                ref={dec}
              ></textarea>
              <input ref={web} type="text" placeholder={t("AddProect.7")} />
              <label>{t("More.3")}</label>
              <div className="investment">
                <input ref={invDo} type="number" placeholder="do" />
                <input ref={invpos} type="number" placeholder="posle" />
              </div>
              <label>{t("More.4")}</label>
              <input ref={revan} type="number" />
              <label>{t("More.6")}</label>
              <div className="investment">
                <input ref={LumpDo} type="number" placeholder="do" />
                <input ref={lumpPos} type="number" placeholder="posle" />
              </div>
              <label>{t("More.7")}</label>
              <input ref={profit} type="numner" />
              <button onClick={HandelPost}>{t("AddProect.8")} </button>
            </form>
          </Inputs>
          <AddImg>
            <form action="" method="post" enctype="multipart/form-data">
              <input
                type="file"
                id="upload-photo"
                name="files[]"
                multiple="multiple"
                accept="image/*"
                onChange={UploadProject}
              />
              <label id="upload-photo-label" for="upload-photo">
                <img src={addImg} alt="photo" />
              </label>
            </form>
          </AddImg>
        </Title>
      </Wrapper>
    </>
  );
}

export default AddProect;
