import styled from "styled-components";
import Back from "../../../assets/images/client/consultation/human.png"
import Back1 from "../../../assets/images/client/consultation/back.png"

export const Buttons = styled.div`
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    list-style: none;
    width: 90%;
   }
`

export const Button = styled.button`
padding: 10px 30px;
background: #FAFAFA;
box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.03);
border-radius: 50px;
border: 1px solid #FAFAFA;
cursor: pointer;
transition: .65s;
margin-top: 15%;
&:hover {
    background: #297D3B;
    color: white;
}
`
export const Li = styled.li`
button {
    background-color: #297D3B;
    color: white;
    padding: 10px 30px;
    border-radius:50px;
    border: 1px solid #297D3B;
    transition: .65s;
    margin-top: 15%;


    &:hover {
        background-color: #FAFAFA;
        border: 1px solid #FAFAFA;
        color: black;
    }
}
`

export const Heading = styled.h2`
font-family: "Open sans";
font-size: 30px;
padding-bottom: 2%;
`   





