import styled from "styled-components";
import backImg from "../../../assets/backgaround/backImg.png";

export const Wrapper = styled.div`
  .headerTexts {
    font-size: 35px;
    font-weight: 600;
    margin-top: 3%;
  }
  .cols {
    background-color: #fafafa;
    margin-top: 7%;
    border-radius: 20px;
    @media screen and (max-width: 767px) {
      width: 70%;
      margin: 7% auto 0;
    }
    @media screen and (max-width: 545px) {
      width: 100%;
      margin: 7% auto 0;
    }
    h2 {
      font-size: 35px;
    }
    img {
      width: 100%;
      height: 300px;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
    }
    .texts {
      padding: 0 20px 20px;
      button {
        background: #297d3b;
        padding: 13px 30px;
        color: white;
        letter-spacing: 1px;
        font-size: 13px;
        border: 1px solid #297d3b;
        cursor: pointer;
        border-radius: 20px;
      }
    }
  }
  .moreBtn {
    margin-top: 3%;
    .btn {
      display: flex;
      justify-content: center;
      align-items: center;
      button {
        background: #ebebeb;
        padding: 13px 30px;
        color: black;
        cursor: pointer;
        cursor: pointer;
        letter-spacing: 1px;
        font-size: 13px;
        border: 1px solid #ebebeb;
        border-radius: 20px;
        transition: 0.65s;
        &:hover {
          background-color: #297d3b;
          color: white;
        }
      }
    }
  }
`;
export const WrapperDiv = styled.div`
  background-image: url(${backImg});
  background-repeat: no-repeat;
  background-position-x: right;
  background-position-y: bottom;
`;
