import styled from "styled-components";

export const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 860px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .userInfors {
    width: 47%;
    @media screen and (max-width: 1230px) {
      width: 50%;
    }
    @media screen and (max-width: 860px) {
      width: 100%;
    }
    img {
      width: 90%;
      height: 300px;
      border-radius: 20px;
      @media screen and (max-width: 860px) {
        width: 100%;
      }
    }
  }
  .userDec {
    width: 50%;
    border-top: 1px solid grey;
    padding-top: 2%;
    margin-top: 5%;
    @media screen and (max-width: 1230px) {
      width: 50%;
    }
    @media screen and (max-width: 860px) {
      width: 100%;
    }

    h3 {
      font-size: 35px;
      margin: 0;
    }
    p {
      font-size: 19px;
      line-height: 25px;
      width: 90%;
      @media screen and (max-width: 1230px) {
        width: 100%;
      }
    }
    button {
      padding: 15px 25px;
      border: 1px solid #297d3b;
      background-color: #297d3b;
      cursor: pointer;
      border-radius: 30px;
      color: white;
      font-weight: bold;
      font-size: 20px;

      @media screen and (max-width: 365px) {
        font-size: 16px;
      }
    }
  }
`;
