import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { Api_Url } from "../../services/api";

export const GetUserCategory = createAsyncThunk(
  "UserCategory/get",
  async () => {
    return await axios
      .get(`${Api_Url}/user_category`)
      .then((response) => response.data);
  }
);

const UserCategorySlice = createSlice({
  name: "UserCategory",
  initialState: {
    getUserCategory: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
  },
  extraReducers: {
    [GetUserCategory.pending]: (state, action) => {
      state.getUserCategory.Loading = true;
    },
    [GetUserCategory.fulfilled]: (state, action) => {
      state.getUserCategory.Error = false;
      state.getUserCategory.Success = true;
      state.getUserCategory.Loading = false;
      state.getUserCategory.Data = action.payload;
    },
    [GetUserCategory.rejected]: (state, action) => {
      state.getUserCategory.Error = true;
      state.getUserCategory.Success = false;
      state.getUserCategory.Loading = false;
      state.getUserCategory.Data = [];
    },
  },
});

export default UserCategorySlice.reducer;
