import styled from "styled-components";
import FirstImg from "./../../../../assets/images/client/home-hero/city.png"
export const Wrapper = styled.div`
    display: flex;
    justify-content: space-between;
    @media only screen and (max-width:920px) {
        flex-direction: column;
    }
`
export const WrapperLeft = styled.div`
    width: 40%;
    h1{
        padding-top: 60px;
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 600;
        font-size: 40px;
        line-height: 54px;
        letter-spacing: 0.01em;
        color: #242222;
        width: 110%;
    }
    span{
        color:#297D3B;
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 600;
        font-size: 35px;
        line-height: 48px;
        letter-spacing: 0.01em;
    }
    p{
        padding-top: 20px;
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 27px;
        letter-spacing: 0.01em;
        width: 90%;
        color: #242222;
    }
    @media only screen and (max-width : 920px) {
        width: 100%;
        h1{
           font-size: 25px;
           line-height: 34px;
           width: 100%;
        }
        span{
            font-size: 25px;
            line-height: 34px;   
        }
        p{
            font-size: 14px;
           line-height: 19px; 
           padding-top: 15px;
           width: 100%;
        }
}
`
export const FirstButtonMedia = styled.div`
display: none;
@media only screen and (max-width: 526px) {
    display: block;
    font-weight: 600;
    font-size: 14px;
    transition: .65s;
    cursor: pointer;
    font-family: 'Open Sans';
    font-style: normal;
    width: 100%;
    padding-top: 23px;
    padding-bottom: 23px;
    background-color: #297D3B;
    color: white;
    border: 2px solid #297D3B;
    border-radius: 40px;
    margin-bottom: 20px;
    &:hover {
    background-color: #fff;
    color: #297D3B;
}
}
`
export const SecondButtonMedia = styled.div`
display: none;
@media only screen and (max-width: 526px) {
    display: block;
    font-weight: 600;
    font-size: 14px;
    transition: .65s;
    cursor: pointer;
    font-family: 'Open Sans';
    font-style: normal;
    width: 100%;
    background-color: #297D3B;
    color: white;
    border: 2px solid #297D3B;
    border-radius: 40px;
    padding-top: 23px;
    padding-bottom: 23px;
    &:hover {
    background-color: #fff;
    color: #297D3B;
}
}
`
export const ButtonMedia = styled.div`
    display: none;
    @media only screen and (max-width: 526px) {
        display: flex;
        text-align: center;
        flex-direction: column;
        margin-top: 23px;
}
`
export const SecondImg = styled.div`
    background-image: url(${FirstImg});
    background-size: cover;
    width: 50%;
    margin-top: 70px;
    @media only screen and (max-width:920px) {
        width: 100%;
        img{
            width: 100%;
        }
}
`
export const Buttons = styled.div`
   display: flex;
   padding-top: 35px;
`
export const FirstButton = styled.button`
    font-weight: 600;
    font-size: 14px;
    transition: .65s;
    cursor: pointer;
    font-family: 'Open Sans';
    font-style: normal;
    width: 200px;
    height: 55px;
    background-color: #297D3B;
    color: white;
    border: 2px solid #297D3B;
    border-radius: 10px;
    &:hover {
    background-color: #fff;
    color: #297D3B;
}
@media only screen and (max-width: 526px) {
     display: none;
}
`
export const SecondButton = styled.button`
    font-weight: 600;
    font-size: 14px;
    transition: .65s;
    cursor: pointer;
    font-family: 'Open Sans';
    font-style: normal;
    width: 200px;
    height: 55px;
    background-color: #297D3B;
    color: white;
    border: 2px solid #297D3B;
    border-radius: 10px;
    margin-left: 20px;
    &:hover {
    background-color: #fff;
    color: #297D3B;
}
@media only screen and (max-width: 526px) {
        display: none;
}
`