import React, { useEffect } from "react";
import { WrapperContainer } from "../../../App-styled";
import img from "../../../assets/Frame2.png";
import { Wrapper } from "./styled-index";
import Slider from "react-slick";
import { useDispatch, useSelector } from "react-redux";
import { GetNews } from "../../../store/client/news";

function News() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetNews());
  }, []);
  const Data = useSelector((state) => state?.news?.getNews?.Data?.data);
  return (
    <>
      <WrapperContainer>
        <Wrapper>
          <Slider {...settings}>
            {Data?.map((elem, index) => (
              <div key={index}>
                <img src={elem.img} alt="photo" />
              </div>
            ))}
          </Slider>
        </Wrapper>
      </WrapperContainer>
    </>
  );
}

export default News;
