import imgs from "../../../assets/images/client/singin/cat.png";
import { useTranslation } from "react-i18next";
import {
  Title,
  Wrapper,
  About,
  Button,
  Heading,
  Img,
  ImgDiv,
} from "../../../page/client/singin/styled-index";
import { useDispatch, useSelector } from "react-redux";
import { useState } from "react";
import { SingIn } from "../../../store/client/loginauth";
function Singin() {
  const { t, i18n } = useTranslation();
  const [name, setname] = useState("");
  const [password, setPassword] = useState("");
  const dispatch = useDispatch();
  const HandelClick = (e) => {
    e.preventDefault();
    const userData = {
      name,
      password,
    };
    dispatch(SingIn(userData));
  };
  const login = useSelector((state) => state.login.error)
  return (
    <>
      <Wrapper>
        <Title>
          <About>
            <Heading>
              <h1>
                {t("SingIn.0")} <span>IN</span>BUSINESS
              </h1>
            </Heading>
            <form onSubmit={HandelClick}>
              <input
                onChange={(e) => setname(e.target.value)}
                type="text"
                placeholder={t("SingIn.2")}
              />
              <input
                onChange={(e) => setPassword(e.target.value)}
                type="password"
                placeholder={t("SingIn.3")}
              />
              {login == "Rejected" ? <><p className="errorSubtext">{t("SingIn.4")}</p></> : null}
              <Button type="submit">{t("SingIn.1")}</Button>
            </form>
          </About>
          <ImgDiv>
            <Img src={imgs} alt="photo" />
          </ImgDiv>
        </Title>
      </Wrapper>
    </>
  );
}

export default Singin;
