import React, { useState } from "react";
import { WrapperContainer } from "../../../App-styled";
import { Wrapper } from "./styled-index";
import user from "../../../assets/images/client/usermore/user.png";
import Box from "@mui/material/Box";
import Rating from "@mui/material/Rating";
import Typography from "@mui/material/Typography";
import ModalLinks from "./modal";
import CardComponent from "../home/new-project/card";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { GetConsul } from "../../../store/client/consul";
import { t } from "i18next";

function UserMoreComp() {
  const [value, setValue] = React.useState(2);
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetConsul());
  }, []);
  const Dat = useSelector((state) => state.consul?.getConsul?.Data?.data);
  const MoreId = window.localStorage.getItem("moreId");
  return (
    <>
      <WrapperContainer>
        <Wrapper>
          {Dat?.map((elem, index) =>
            elem.id == MoreId ? (
              <>
                <div className="userInfors">
                  <h2>{elem.company_name}</h2>
                  <img src={elem.project_img} alt="photo" />
                  <h2>{t("More.11")}</h2>
                  <p>Маркетолог </p>
                  <p>
                    {t("UserMore.0")} {elem.experience} {t("More.19")}
                  </p>
                  <p>{t("UserMore.1")} 4.5</p>
                </div>
                <div className="userDec">
                  <h3>{t("More.17")}</h3>
                  <p>{elem.comments}</p>
                  <button onClick={handleOpen}>
                    {t("UserMore.2")}
                  </button>
                </div>
              </>
            ) : null
          )}
        </Wrapper>
        <ModalLinks
          open={open}
          setOpen={setOpen}
          handleClose={handleClose}
          Data={Dat}
        />
        <CardComponent />
      </WrapperContainer>
    </>
  );
}

export default UserMoreComp;
