import * as React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { Links, ModalHeader } from "./styled-index";
import telegram from "../../../../assets/images/client/linkIcons/telegram.svg";
import insta from "../../../../assets/images/client/linkIcons/insta.svg";
import phone from "../../../../assets/images/client/linkIcons/phone.png";
import user from "../../../../assets/images/client/addProect/profile2.png";
import { Wrapper } from "./styled-index";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

export default function ModalLinks({ open, setOpen, handleClose, Data }) {
  const MoreId = window.localStorage.getItem("moreId");
  return (
    <div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Wrapper>
            <i onClick={handleClose} class="bx bx-x"></i>
            <ModalHeader>
              <img src={user} alt="user" />
              <h3>Александра</h3>
            </ModalHeader>
            <Links>
              {Data?.map((elem, index) =>
                elem.id == MoreId ? (
                  <>
                    <a href={elem.tg_username}>
                      <img src={telegram} alt="photo" />
                    </a>
                    <a href={elem.inst_username}>
                      <img src={insta} alt="photo" />
                    </a>
                    <a href={`tel: ${elem.phone_number}`}>
                      <img src={phone} alt="photo" />
                    </a>
                  </>
                ) : null
              )}
            </Links>
          </Wrapper>
        </Box>
      </Modal>
    </div>
  );
}
