import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import Cookies from "universal-cookie";
const cookies = new Cookies();

export const UploadImage = createAsyncThunk("Profile/upload", async (e) => {
  const formData = new FormData();
  formData.append("file", e.target.files[0]);
  formData.append("upload_preset", "br3m8rzo");
  try {
    return await axios
      .post("https://api.cloudinary.com/v1_1/dibvlfm8v/upload", formData)
      .then((response) => response?.data.secure_url);
  } catch (error) {
    return error;
  }
});
export const UploadImage2 = createAsyncThunk("Profile/upload2", async (e) => {
  const formData = new FormData();
  formData.append("file", e.target.files[0]);
  formData.append("upload_preset", "br3m8rzo");
  try {
    return await axios
      .post("https://api.cloudinary.com/v1_1/dibvlfm8v/upload", formData)
      .then((response) => response?.data.secure_url);
  } catch (error) {
    return error;
  }
});
export const ProfilePost = createAsyncThunk("Profile/post", async (body) => {
  return axios
    .post("https://startap.supersiteuz.com/superUser/register", body)
    .then((res) => {
      console.log(res);
      if (res?.data?.message === "SuperUsers created") {
        cookies.set("userCreate", true);
        window.localStorage.setItem("Diss", true);
        cookies.set("userID", res?.data?.data?.id);
      }
    });
});
export const PutProfile = createAsyncThunk(
  "Profile/put",
  async ({ body, id }) => {
    return await axios
      .put(`https://startap.supersiteuz.com/superUser/${id}`, body)
      .then((response) => console.log(response.data));
  }
);
export const GetProdfile = createAsyncThunk("Profile/get", async () => {
  return axios
    .get("https://startap.supersiteuz.com/superUser")
    .then((res) => res);
});
const ProfileSlice = createSlice({
  name: "Profile",
  initialState: {
    uploadProfile: {
      Error: false,
      Loading: false,
      Success: false,
      data: "",
    },
    uploadProfile2: {
      Error: false,
      Loading: false,
      Success: false,
      data2: "",
    },
    postPrifile: {
      Error: false,
      Loading: false,
      Success: false,
    },
    putPrifile: {
      Error: false,
      Loading: false,
      Success: false,
    },
    getProf: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
  },
  extraReducers: {
    [UploadImage.pending]: (state, action) => {
      state.uploadProfile.Loading = true;
    },
    [UploadImage.fulfilled]: (state, action) => {
      state.uploadProfile.Error = false;
      state.uploadProfile.Success = true;
      state.uploadProfile.Loading = false;
      state.uploadProfile.data = action.payload;
    },
    [UploadImage.rejected]: (state, action) => {
      state.uploadProfile.Error = true;
      state.uploadProfile.Success = false;
      state.uploadProfile.Loading = false;
    },
    [UploadImage2.pending]: (state, action) => {
      state.uploadProfile2.Loading = true;
    },
    [UploadImage2.fulfilled]: (state, action) => {
      state.uploadProfile2.Error = false;
      state.uploadProfile2.Success = true;
      state.uploadProfile2.Loading = false;
      state.uploadProfile2.data2 = action.payload;
    },
    [UploadImage2.rejected]: (state, action) => {
      state.uploadProfile2.Error = true;
      state.uploadProfile2.Success = false;
      state.uploadProfile2.Loading = false;
    },
    [ProfilePost.pending]: (state, action) => {
      state.postPrifile.Loading = true;
    },
    [ProfilePost.fulfilled]: (state, action) => {
      state.postPrifile.Error = false;
      state.postPrifile.Success = true;
      state.postPrifile.Loading = false;
    },
    [ProfilePost.rejected]: (state, action) => {
      state.postPrifile.Error = true;
      state.postPrifile.Success = false;
      state.postPrifile.Loading = false;
    },
    [PutProfile.pending]: (state, action) => {
      state.putPrifile.Loading = true;
    },
    [PutProfile.fulfilled]: (state, action) => {
      state.putPrifile.Error = false;
      state.putPrifile.Success = true;
      state.putPrifile.Loading = false;
    },
    [PutProfile.rejected]: (state, action) => {
      state.putPrifile.Error = true;
      state.putPrifile.Success = false;
      state.putPrifile.Loading = false;
    },
    [GetProdfile.pending]: (state, action) => {
      state.getProf.Loading = true;
    },
    [GetProdfile.fulfilled]: (state, action) => {
      state.getProf.Error = false;
      state.getProf.Success = true;
      state.getProf.Loading = false;
      state.getProf.Data = action.payload;
    },
    [GetProdfile.rejected]: (state, action) => {
      state.getProf.Error = true;
      state.getProf.Success = false;
      state.getProf.Loading = false;
      state.getProf.data = [];
    },
  },
});

export default ProfileSlice.reducer;
