import styled from "styled-components";
export const Wrapper = styled.div`
  font-family: "Open sans";
  margin-top: 3%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100% !important;
  h3,
  p,
  button,
  h4 {
    margin-left: 5%;
  }
`;

export const Buttons = styled.div`
  width: 100%;
  text-align: center;
  button{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 23px;
    letter-spacing: 0.01em;
    color: #242222;
    background: #EBEBEB;
    box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.03);
    border-radius: 40px;
    border: none;
    margin-top: 20px;
    transition: .65s;
    width: 200px;
    height: 55px;
    @media screen and (max-width:920px) {
      text-align: center;
        }
   }
    button:hover{
     background-color: #297D3B;
     color: white;
 
    }
`

export const SecondUl = styled.div`
  ul{
    padding-top: 20px;
    list-style: none;
    display: flex;
   justify-content: space-between;
   flex-wrap: wrap;
  }
  li{
    background: #FAFAFA;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    margin-left: -30px;
    padding-bottom: 40px;
    margin-bottom: 20px;
    transition: .1s;
  }
  li:hover{
        opacity: 0.8;
  }
  img{
  width: 418px;
  height: 251px;
  @media only screen and (max-width:920px) {
        width: 100%;
        img{
          width: 100%;
        }
}
   }
   h3{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 27px;
    letter-spacing: 0.01em;
    color: #242222;
    margin-left: 20px;
   }
   p{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.01em;
    color: #242222;
    margin-left: 20px;
   }
   a{
    text-decoration: none;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 125%;
    letter-spacing: 0.03em;
    color: white;
    padding: 15px 40px;
    background: #297D3B;
    border-radius: 40px;
    margin-left: 20px;
   }
`