import styled from "styled-components";

export const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;
  img {
    display: flex;
    align-items: center;
    padding-top: 18%;
  }
  @media only screen and (max-width: 838px) {
    img {
      display: none;
    }
  }
`;
export const Form = styled.form`
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 3%;
  .errorSubtext {
    color: red;
    font-size: 20px;
    font-weight: 600;
    margin: 2% 0;
  }
  input {
    width: 93%;
    outline: none;
    margin: 2% 0;
    border: 1px solid #f5f5f5;
    background-color: #f5f5f5;
    padding: 3% 3%;
    border-radius: 10px;
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    letter-spacing: 0.01em;
    color: #808080;
  }
  select {
    width: 99%;
    outline: none;
    margin: 2% 0;
    border: 1px solid #f5f5f5;
    background-color: #f5f5f5;
    padding: 3% 3%;
    border-radius: 10px;
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    letter-spacing: 0.01em;
    color: #808080;
  }
  @media only screen and (max-width: 838px) {
    width: 100%;
    input {
      width: 90%;
    }
  }
`;
export const Heading = styled.div`
  border-bottom: 0.5px solid #808080;
  width: 100%;
  span {
    color: #297d3b;
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 600;
    font-size: 35px;
    line-height: 48px;
    letter-spacing: 0.01em;
  }
  h1 {
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 600;
    font-size: 35px;
    line-height: 48px;
    letter-spacing: 0.01em;
    color: #242222;
  }
  @media only screen and (max-width: 838px) {
    h1 {
      font-size: 20px;
      line-height: 27px;
    }
    span {
      font-size: 20px;
      line-height: 27px;
    }
  }
`;
export const Button = styled.button`
  padding: 3% 10%;
  margin-top: 2%;
  border-radius: 10px;
  background-color: #297d3b;
  color: #fff;
  border: 2px solid #297d3b;
  font-weight: 600;
  font-size: 14px;
  transition: 0.65s;
  cursor: pointer;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  @media only screen and (max-width: 838px) {
    margin: 0 auto;
  }
  @media only screen and (max-width: 838px) {
    margin-top: 30px;
    padding: 5% 20%;
  }
  &:hover {
    background-color: #fff;
    color: #297d3b;
  }
`;
