import { useTranslation } from "react-i18next";
import {
  Wrapper,
  WrapperLeft,
  Buttons,
  FirstButton,
  SecondButton,
  FirstImg,
  SecondImg,
  FirstButtonMedia,
  SecondButtonMedia,
  ButtonMedia,
} from "./styled-index";
// import {FirstButton,SecondButton} from "./../../../../components/client/singup/styled-index"
import HomeHero from "./../../../../assets/images/client/home-hero/city.png";
import homehero from "./../../../../assets/images/client/home-hero/interview.png";
import NewProject from "../newProject";
import { WrapperContainer } from "../../../../App-styled";
import HomeConsul from "../HomeConsultation";
import Cookies from "universal-cookie";
import { NavLink } from "react-router-dom";
const cookies = new Cookies();
function HomeHeroComponent() {
  const { t, i18next } = useTranslation();
  const userId = cookies.get("userId");
  return (
    <>
      <WrapperContainer>
        <Wrapper>
          <WrapperLeft>
            <h1>
              {t("HomeHero.0")} <span></span>{" "}
            </h1>
            <p>{t("HomeHero.1")} </p>
            <Buttons>
              <NavLink to={"/searchproject"}>
                <FirstButton>{t("HomeHero.2")}</FirstButton>
              </NavLink>
              {userId ? (
                <>
                  <NavLink to={"/addProect"}>
                    <SecondButton>{t("HomeHero.3")}</SecondButton>
                  </NavLink>
                </>
              ) : null}
            </Buttons>
          </WrapperLeft>
          <SecondImg>
            <img src={homehero} width={610} alt="Home Hero image" />
          </SecondImg>
          <ButtonMedia>
            <NavLink to={"/searchproject"}>
              <FirstButtonMedia>{t("HomeHero.2")}</FirstButtonMedia>
            </NavLink>
            {userId ? (
              <>
                <NavLink to={"/addproect"}>
                  <SecondButtonMedia>{t("HomeHero.3")}</SecondButtonMedia>
                </NavLink>
              </>
            ) : null}
          </ButtonMedia>
        </Wrapper>
      </WrapperContainer>
      <NewProject />
      <HomeConsul />
    </>
  );
}
export default HomeHeroComponent;
