import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export const GetConsul = createAsyncThunk("Consul/get", async () => {
  return axios
    .get("https://startap.supersiteuz.com/superUser")
    .then((res) => res);
});

const ConsulSlice = createSlice({
  name: "Consul",
  initialState: {
    getConsul: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
  },
  extraReducers: {
    [GetConsul.pending]: (state, action) => {
      state.getConsul.Loading = true;
    },
    [GetConsul.fulfilled]: (state, action) => {
      state.getConsul.Error = false;
      state.getConsul.Success = true;
      state.getConsul.Loading = false;
      state.getConsul.Data = action.payload;
    },
    [GetConsul.rejected]: (state, action) => {
      state.getConsul.Error = true;
      state.getConsul.Success = false;
      state.getConsul.Loading = false;
      state.getConsul.Data = [];
    },
  },
});


export default ConsulSlice.reducer