import styled from "styled-components";
import BackImg from "../../../assets/images/client/singin/Ellisse.png"

export const Wrapper = styled.div`
max-width: 1310px;
width: 100%;
padding: 0 10px;
margin: 0 auto;
@media screen and (max-width: 820px){
    width: 100%;
}
`

export const About = styled.div`
width: 40%;

.errorSubtext{
color: red;
font-size: 20px;
font-weight: 600;
margin: 2% 0;
}
@media screen and (max-width: 820px){
    width: 100%;
    text-align: center;
}
form{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 6%;
    padding: 0 4% 0 2%;

    input{
        width: 80%;
        outline: none;
        margin: 2% 0;
        border: 1px solid #F5F5F5;
        background-color: #F5F5F5 ;
        padding: 4% 4%;
        border-radius: 10px;
        @media screen and (max-width: 820px){
        width: 90%;
       }
       
    }   
}
`
export const Title = styled.div`
display: flex;
align-items: flex-start;
justify-content: space-between;
margin-top: 6%;


h1{
    font-family: 'Open Sans', sans-serif;
    span{
        color: #297D3B;
    }
}
`
export const Button = styled.button`
padding: 3% 10%;
margin-top: 2%;
border-radius: 10px;
background-color: #297D3B;
color: white;
border: 2px solid #297D3B;
font-weight: 600;
font-size: 100%;
transition: .65s;
cursor: pointer;

@media screen and (max-width: 820px){
width: 60%;
margin: 0 auto;
margin-top: 2%;
}

&:hover {
    background-color: white;
    color: #297D3B;
}
`
export const Heading = styled.div`
border-bottom: 1px solid #808080;
@media screen and (max-width: 426px){
    border-bottom: none;
}
h1{
    @media screen and (max-width: 426px){
    font-style: normal;
     font-weight: 600;
     font-size: 20px;
     line-height: 27px;
     letter-spacing: 0.01em;
    }
}
`

export const Img = styled.img`
@media screen and (max-width: 820px){
    display: none;
}
`


export const ImgDiv = styled.div`
background-image: url(${BackImg});
background-repeat: no-repeat;
height: 450px;
background-position-y: 150px;
background-position-x: 300px;
`

export const Window = styled.div`

`