import { Wrapper, Form, Heading, Button } from "./styled-index";
import SignUp from "./../../../assets/images/client/singin/cat.png";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { SinUpUsers } from "../../../store/client/auth";
import { GetCategory } from "../../../store/client/addProect";
import { GetUserCategory } from "../../../store/client/userCateg";
function SingUpComponent() {
  const { t, i18next } = useTranslation();
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [phone_number, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [repeatPassword, setRepeat_password] = useState("");
  const dispatch = useDispatch();
  useEffect(() => {}, []);
  const HandleClick = (e) => {
    e.preventDefault();
    const userData = {
      username,
      email,
      phone_number,
      password,
      repeatPassword,
    };
    dispatch(SinUpUsers(userData));
  };
  const users = useSelector((state) => state.users.error);
  useEffect(() => {
    dispatch(GetUserCategory());
  }, []);
  const Data = useSelector((state) => state?.userCateg?.getUserCategory?.Data);
  return (
    <Wrapper>
      <Form onSubmit={HandleClick}>
        <Heading>
          <h1>
            {t("SingUp.0")} <span>IN</span>BUSINESS
          </h1>
        </Heading>
        <input
          onChange={(e) => setUsername(e.target.value)}
          type="text"
          placeholder={t("SingUp.1")}
          required
        />
        <input
          onChange={(e) => setEmail(e.target.value)}
          type="email"
          placeholder={t("SingUp.2")}
          required
        />
        <input
          onChange={(e) => setPhone(e.target.value)}
          type="number"
          placeholder={t("SingUp.3")}
          required
        />
        <input
          onChange={(e) => setPassword(e.target.value)}
          type="password"
          placeholder={t("SingUp.4")}
          required
        />
        <input
          onChange={(e) => setRepeat_password(e.target.value)}
          type="password"
          placeholder={t("SingUp.5")}
          required
        />
        {users == "Rejected" ? (
          <>
            <p className="errorSubtext">{t("SingUp.7")}</p>
          </>
        ) : null}
        <Button type="submit">{t("SingUp.5")}</Button>
      </Form>
      <img src={SignUp} width={500} height={258} alt="Sing up image cat" />
    </Wrapper>
  );
}

export default SingUpComponent;
