import styled from "styled-components";

export const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10% 0 5%;

  .buttons {
    display: flex;
    flex-direction: column;
    width: 50%;
    @media screen and (max-width: 775px) {
      width: 80%;
    }
    @media screen and (max-width: 500px) {
      width: 100%;

      button {
        font-size: 25px !important;
      }
    }
    button {
      font-size: 30px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 15px 15px;
      background-color: #fafafa;
      color: #297d3b;
      border: 0.5px solid #ebebeb;
      border-radius: 50px;
      transition: all 0.65s;
      box-shadow: 1px 3px 5px 0px #00000008;
      width: 100%;

      &:hover {
        background-color: #297d3b;
        color: white;
      }

      span {
        font-size: 30px;
      }
    }
    button:nth-child(1) {
      margin-bottom: 10%;
    }
  }
`;
