import styled from "styled-components";

export const Wrapper = styled.div`
display: none;
 @media only screen and (max-width : 920px) {
    position: fixed;
    display: flex;
    width: 80%;
    height: 100%;
    background-color: #fff;
    flex-direction: column;
    padding: 10px;
    padding-top: 30px;
    left: ${({menuOpen}) => menuOpen ? "-850px": "0px"};
    transition: 2s;
    z-index: 1000;
    top: 0;
    img:nth-child(2){
        transform: rotate(180deg);
    }
    }

`
export const WrapperTop = styled.div`
    display: flex;
    justify-content: space-between;
`
export const WrapperItem = styled.li`
    padding: 15px;
    border-bottom: 2px solid #7b7a7a2b;
    span{
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 0.01em;
        color: #242222;
    }
`
export const WrapperList = styled.ul`
    margin: 0;
    padding: 0;
    list-style-type: none;
    margin-top: 30px;
`

export const MenuFooter = styled.div`
    margin-top: 20px;
    ul{
        width: 75%;
        display: flex ;
        justify-content: space-around;
        list-style-type: none;
    }
   
   li:nth-child(1){
    padding-right: 5px;
   }
   li:nth-child(2){
    padding-right: 5px;
   }
   hr{
    height: 15px;
    width: 1px;
    background-color: #CFD8DC;
    color: #CFD8DC;
    display: flex;
    border: transparent;
   }
   li{
    button{
        font-family: 'Open Sans';
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 20px;
            display: flex;
            align-items: center;
            color: #7B8E98;
            background-color: transparent;
            border: transparent;
    }
   }
`