import { useState } from "react";
import { useEffect } from "react";
import { Col, Row } from "react-grid-system";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { WrapperContainer } from "../../../../App-styled";
import { GetCategory, GetProject } from "../../../../store/client/addProect";
import News from "../../news";
import { Wrapper, WrapperDiv } from "./styled-index";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import "./styles.css";
import { NavLink } from "react-router-dom";
function NewProject() {
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetCategory());
    dispatch(GetProject());
  }, []);
  const Category = useSelector(
    (state) => state?.addProject?.getCategory?.Data?.data
  );
  const data = useSelector(
    (state) => state?.addProject?.getProject?.Data?.data
  );

  const [value, setValue] = useState("");
  const [val, setVal] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(event.target.id);
    setVal(newValue);
  };
  return (
    <>
      <WrapperDiv>
        <WrapperContainer>
          <Wrapper>
            <h2 className="headerText">{t("NewPro.0")}</h2>
            <Tabs
              value={val}
              onChange={handleChange}
              aria-label="basic tabs example"
            >
              {Category?.map((elem, index) => (
                <Tab key={index} id={elem.title} label={elem.title} />
              ))}
            </Tabs>
            <Row style={{ margin: 0 }}>
              {value == "" ? (
                <>
                  {data?.map((elem) => (
                    <Col lg={4} md={6} sm={12}>
                      <div className="cols">
                        <img src={elem.img} alt="photo" />
                        <div className="texts">
                          <h3>{elem?.categoryId?.title}</h3>
                          <p>{elem.company_name}</p>
                          <p>Возраст бизнеса - {elem.business_age} лет</p>
                          <p>
                            Количество сотрудников - {elem.employees_number}
                          </p>
                          <p>Окупаемость - {elem.payback} года</p>
                          <p>Причина продажи - {elem.reason_for_sale}</p>
                          <NavLink
                            to={"/more"}
                            onClick={() =>
                              window.localStorage.setItem("moreIDD", elem.id)
                            }
                          >
                            <button>{t("Search.3")}</button>
                          </NavLink>
                        </div>
                      </div>
                    </Col>
                  ))}
                </>
              ) : (
                <>
                  {data?.map((elem) =>
                    elem?.categoryId?.title == value ? (
                      <Col lg={4} md={6} sm={12}>
                        <div className="cols">
                          <img src={elem.img} alt="photo" />
                          <div className="texts">
                            <h3>{elem?.categoryId?.title}</h3>
                            <p>{elem.company_name}</p>
                            <p>
                              {t("Card.2")} {elem.business_age} {t("Card.10")}
                            </p>
                            <p>
                              {t("Card.3")} {elem.employees_number}
                            </p>
                            <p>
                              {t("Card.5")} {elem.payback} {t("Card.11")}
                            </p>
                            <p>
                              {t("Card.6")} {elem.reason_for_sale}
                            </p>
                            <NavLink
                              to={"/more"}
                              onClick={() =>
                                window.localStorage.setItem("moreIDD", elem.id)
                              }
                            >
                              <button>{t("Search.3")}</button>
                            </NavLink>
                          </div>
                        </div>
                      </Col>
                    ) : null
                  )}
                </>
              )}
            </Row>
          </Wrapper>
          <News />
        </WrapperContainer>
      </WrapperDiv>
    </>
  );
}

export default NewProject;
