import React from "react";
import { useEffect } from "react";
import { Col, Row } from "react-grid-system";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import { WrapperContainer } from "../../../App-styled";
import { GetCategory, GetProject } from "../../../store/client/addProect";
import News from "../news";
import { Wrapper, WrapperDiv } from "./styled-index";

function FranchiseComp() {
  const data = [1, 2, 3, 4, 5, 6, 7, 8, 9];
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetCategory());
    dispatch(GetProject());
  }, []);
  const datas = useSelector(
    (state) => state?.addProject?.getProject?.Data?.data
  );
  return (
    <>
      <WrapperDiv>
        <WrapperContainer>
          <Wrapper>
            <h2 className="headerTexts">{t("Franchisa.0")}</h2>
            <Row style={{ margin: 0 }}>
              {datas?.map((elem) =>
                elem?.categoryId?.title == "Franchise" ? (
                  <Col lg={4} md={6} sm={12}>
                    <div className="cols">
                      <img src={elem.img} alt="photo" />
                      <div className="texts">
                        <h3>{elem?.categoryId?.title}</h3>
                        <p>{elem.company_name}</p>
                        <p>
                          {t("Card.2")} {elem.business_age} {t("Card.10")}
                        </p>
                        <p>
                          {t("Card.3")} {elem.employees_number}
                        </p>
                        <p>
                          {t("Card.5")} {elem.payback} {t("Card.11")}
                        </p>
                        <p>
                          {t("Card.6")} {elem.reason_for_sale}
                        </p>
                        <NavLink
                          to={"/more"}
                          onClick={() =>
                            window.localStorage.setItem("moreIDD", elem.id)
                          }
                        >
                          <button>{t("Search.3")}</button>
                        </NavLink>
                      </div>
                    </div>
                  </Col>
                ) : null
              )}
            </Row>
            {/* <div className="moreBtn">
              <div className="btn">
                <button>Посмотреть все</button>
              </div> */}
            {/* </div> */}
          </Wrapper>
          <News />
        </WrapperContainer>
      </WrapperDiv>
    </>
  );
}

export default FranchiseComp;
