import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { Api_Url } from "../../services/api";

export const GetCategory = createAsyncThunk("Project/get", async () => {
  return axios.get(`${Api_Url}/category`).then((res) => res);
});
export const Upload = createAsyncThunk("Project/upload", async (e) => {
  const formData = new FormData();
  formData.append("file", e.target.files[0]);
  formData.append("upload_preset", "br3m8rzo");
  try {
    return await axios
      .post("https://api.cloudinary.com/v1_1/dibvlfm8v/upload", formData)
      .then((response) => response?.data.secure_url);
  } catch (error) {
    return error;
  }
});
export const ProjectPost = createAsyncThunk("Project/post", async (body) => {
  return axios
    .post(`${Api_Url}/projects`, body)
    .then((res) => console.log(res));
});

export const GetProject = createAsyncThunk("Project/gets", async () => {
  return axios.get(`${Api_Url}/projects`).then((res) => res);
});

const ProjectSlice = createSlice({
  name: "Project",
  initialState: {
    getCategory: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
    getProject: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
    uploadProject: {
      Error: false,
      Loading: false,
      Success: false,
      data: "",
    },
    postProject: {
      Error: false,
      Loading: false,
      Success: false,
    },
  },
  extraReducers: {
    [GetCategory.pending]: (state, action) => {
      state.getCategory.Loading = true;
    },
    [GetCategory.fulfilled]: (state, action) => {
      state.getCategory.Error = false;
      state.getCategory.Success = true;
      state.getCategory.Loading = false;
      state.getCategory.Data = action.payload;
    },
    [GetCategory.rejected]: (state, action) => {
      state.getCategory.Error = true;
      state.getCategory.Success = false;
      state.getCategory.Loading = false;
      state.getCategory.Data = [];
    },
    [GetProject.pending]: (state, action) => {
      state.getProject.Loading = true;
    },
    [GetProject.fulfilled]: (state, action) => {
      state.getProject.Error = false;
      state.getProject.Success = true;
      state.getProject.Loading = false;
      state.getProject.Data = action.payload;
    },
    [GetProject.rejected]: (state, action) => {
      state.getProject.Error = true;
      state.getProject.Success = false;
      state.getProject.Loading = false;
      state.getProject.Data = [];
    },
    [Upload.pending]: (state, action) => {
      state.uploadProject.Loading = true;
    },
    [Upload.fulfilled]: (state, action) => {
      state.uploadProject.Error = false;
      state.uploadProject.Success = true;
      state.uploadProject.Loading = false;
      state.uploadProject.data = action.payload;
    },
    [Upload.rejected]: (state, action) => {
      state.uploadProject.Error = true;
      state.uploadProject.Success = false;
      state.uploadProject.Loading = false;
    },
    [ProjectPost.pending]: (state, action) => {
      state.postProject.Loading = true;
    },
    [ProjectPost.fulfilled]: (state, action) => {
      state.postProject.Error = false;
      state.postProject.Success = true;
      state.postProject.Loading = false;
    },
    [ProjectPost.rejected]: (state, action) => {
      state.postProject.Error = true;
      state.postProject.Success = false;
      state.postProject.Loading = false;
    },
  },
});

export default ProjectSlice.reducer;
