import * as React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { Links, ModalHeader } from "./styled-index";
import telegram from "../../../../assets/images/client/linkIcons/telegram.svg";
import insta from "../../../../assets/images/client/linkIcons/insta.svg";
import phone from "../../../../assets/images/client/linkIcons/phone.png";
import user from "../../../../assets/images/client/addProect/profile2.png";
import { Wrapper } from "./styled-index";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

export default function ModalLinks({ open, setOpen, handleClose }) {
  return (
    <div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Wrapper>
            <i onClick={handleClose} class="bx bx-x"></i>
            <ModalHeader>
              <img src={user} alt="user" />
              <h3>Profit school</h3>
            </ModalHeader>
            <Links>
              <img src={telegram} alt="photo" />
              <img src={insta} alt="photo" />
              <img src={phone} alt="photo" />
            </Links>
          </Wrapper>
        </Box>
      </Modal>
    </div>
  );
}
