import React from "react";
import { useEffect } from "react";
import { Col, Row } from "react-grid-system";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { WrapperContainer } from "../../../App-styled";
import { GetCategory, GetProject } from "../../../store/client/addProect";
import News from "../news";
import { Wrapper, WrapperDiv } from "./styled-index";

function PartiesComp() {
  const data = [1, 2, 3, 4, 5, 6, 7, 8, 9];
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetCategory());
    dispatch(GetProject());
  }, []);
  const datas = useSelector(
    (state) => state?.addProject?.getProject?.Data?.data
  );
  return (
    <>
      <WrapperContainer>
        <WrapperDiv>
          <h2 className="h2Text">{t("Business.0")}</h2>
          <News />
          <Wrapper>
            <h2 className="headerText">{t("Franchisa.0")}</h2>
            <Row style={{ margin: 0 }}>
              {datas?.map((elem) =>
                elem?.categoryId?.title == "Franchise " ? (
                  <Col lg={4} md={6} sm={12}>
                    <div className="cols">
                      <img src={elem.img} alt="photo" />
                      <div className="texts">
                        <h3>{elem?.categoryId?.title}</h3>
                        <p>{elem.company_name}</p>
                        <p>Возраст бизнеса - {elem.business_age} лет</p>
                        <p>Количество сотрудников - {elem.employees_number}</p>
                        <p>Окупаемость - {elem.payback} года</p>
                        <p>Причина продажи - {elem.reason_for_sale}</p>
                        <button>{t("Search.3")}</button>
                      </div>
                    </div>
                  </Col>
                ) : null
              )}
            </Row>
          </Wrapper>
        </WrapperDiv>
      </WrapperContainer>
    </>
  );
}

export default PartiesComp;
