import { WrapperContainer } from "../../../App-styled";
import Facebook from "../../../assets/images/client/header/facebook.svg";
import Instagram from "../../../assets/images/client/header/instagram.svg";
import Telegram from "../../../assets/images/client/header/telegram.svg";
import Logo from "../../../assets/images/client/header/logo.svg";
import Close from "../../../assets/images/client/header/Close.svg";
import Register from "../../../assets/images/client/header/Register.svg";
import Hamburger from "../../../assets/images/client/header/hamburger.svg";
import MediaMenu from "./media-menu";
import {
  WrapperTop,
  WrapperBottom,
  WrapperList,
  WrapperItem,
  WrapperNetWorker,
  WrapperBottomCenter,
  WrapperBottomCenterIcons,
  WrapperTopMedia,
  WrapperTopMediaLeft,
} from "./styled-index";
import { useTranslation } from "react-i18next";
import { NavLink } from "react-router-dom";
import { DataLink } from "./dataLink";
import LanguageHeader from "./header-language";
import { useRef, useState } from "react";
import Cookies from "universal-cookie";
function Header() {
  const cookies = new Cookies();
  const { t, i18next } = useTranslation();
  const [menu, setMenu] = useState(false);
  const Overlay = useRef();
  const HandleOpen = () => {
    setMenu(true);
    Overlay.current.style.display = "block";
  };
  const HandleClose = () => {
    setMenu(false);
    Overlay.current.style.display = "none";
  };
  const token = cookies.get("autToken");
  const userId = cookies.get("userId");
  return (
    <>
      <WrapperContainer>
        <WrapperTopMedia>
          <img src={Logo} width={152} height={34} alt="logo" />
          <WrapperTopMediaLeft>
            <NavLink to={"/medialogin"}>
              <img src={Close} width={24} height={21} alt="header icon Close" />
            </NavLink>
            <img
              onClick={HandleOpen}
              src={Hamburger}
              width={30}
              height={19}
              alt="header icon Close"
            />
          </WrapperTopMediaLeft>
        </WrapperTopMedia>
        <WrapperTop>
          <WrapperList>
            {DataLink.map((elem, index) => (
              <WrapperItem key={index}>
                <NavLink to={elem.link} style={{ textDecoration: "none" }}>
                  <span>{t(elem.title)}</span>
                </NavLink>
              </WrapperItem>
            ))}
          </WrapperList>
          <LanguageHeader />
          <WrapperNetWorker>
            <a href="#">
              <img
                src={Facebook}
                width={20}
                height={20}
                alt="header icon facebook"
              />
            </a>
            <a href="#">
              <img
                src={Instagram}
                width={20}
                height={20}
                alt="header icon Instagram"
              />
            </a>
            <a href="#">
              <img
                src={Telegram}
                width={20}
                height={20}
                alt="header icon Telegram"
              />
            </a>
          </WrapperNetWorker>
        </WrapperTop>
        <WrapperBottom>
          <img
            style={{ marginTop: "20px" }}
            src={Logo}
            width={213}
            height={25}
            alt="logo"
          />
          <WrapperBottomCenter>
            <input type="text" placeholder={t("Header.6")} />
          </WrapperBottomCenter>
          <WrapperBottomCenterIcons>
            {token ? (
              <></>
            ) : (
              <NavLink to={"/medialogin"} style={{ textDecoration: "none" }}>
                <div>
                  <img
                    src={Close}
                    width={28}
                    height={28}
                    alt="header icon Close"
                  />
                </div>
                <span>{t("Header.7")}</span>
              </NavLink>
            )}
          </WrapperBottomCenterIcons>
          <WrapperBottomCenterIcons>
            {token ? (
              <>
                <NavLink style={{ textDecoration: "none" }} to={"/profile"}>
                  <div>
                    <img
                      src={Register}
                      width={38}
                      height={38}
                      alt="header icon Register"
                    />
                  </div>
                  <span>{t("Header.10")}</span>
                </NavLink>
              </>
            ) : (
              <NavLink style={{ textDecoration: "none" }} to={"/singup"}>
                <div>
                  <img
                    src={Register}
                    width={38}
                    height={38}
                    alt="header icon Register"
                  />
                </div>
                <span>{t("Header.8")}</span>
              </NavLink>
            )}
          </WrapperBottomCenterIcons>
          {userId ? (
            <>
              <NavLink style={{ textDecoration: "none" }} to={"/addProect"}>
                <span>{t("Header.9")}</span>
              </NavLink>
            </>
          ) : null}

          <div ref={Overlay} onClick={HandleClose} className="overlay"></div>
        </WrapperBottom>
      </WrapperContainer>
      <MediaMenu menu={menu} HandleClose={HandleClose} />
    </>
  );
}
export default Header;
