import { configureStore } from "@reduxjs/toolkit";
import UserSlice from "./client/auth";
import loginSlice from "./client/loginauth";
import ProfileSlice from "./client/profile";
import NewsSlice from "./client/news";
import ConsulSlice from "./client/consul";
import ProjectSlice from "./client/addProect";
import SuperLoginSlice from "./client/superlogin";
import UserCategorySlice from "./client/userCateg";

export const store = configureStore({
  reducer: {
    users: UserSlice,
    login: loginSlice,
    profile: ProfileSlice,
    news: NewsSlice,
    consul: ConsulSlice,
    addProject: ProjectSlice,
    superlogin: SuperLoginSlice,
    userCateg: UserCategorySlice,
  },
});
