import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { NavLink } from "react-router-dom";
import { WrapperContainer } from "../../../App-styled";
import imgs from "../../../assets/images/client/singin/cat.png";
import { SuLogin } from "../../../store/client/superlogin";
import {
  About,
  Button,
  Heading,
  Img,
  ImgDiv,
  Title,
  Wrapper,
  WrapperImg,
} from "./styled-index";

function LoginComp() {
  const { t, i18n } = useTranslation();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const dispatch = useDispatch();
  const HandelClick = (e) => {
    e.preventDefault();
    const userData = {
      email,
      password,
    };
    dispatch(SuLogin(userData));
  };
  //   const login = useSelector((state) => state.login.error);
  return (
    <>
      <WrapperImg>
        <WrapperContainer>
          <Wrapper>
            <Title>
              <About>
                <Heading>
                  <h1>
                    {t("SingIn.0")} <span>IN</span>BUSINESS
                  </h1>
                </Heading>
                <form onSubmit={HandelClick}>
                  <input
                    onChange={(e) => setEmail(e.target.value)}
                    type="email"
                    placeholder={t("SingIn.2")}
                  />
                  <input
                    onChange={(e) => setPassword(e.target.value)}
                    type="password"
                    placeholder={t("SingIn.3")}
                  />
                  {/* {login == "Rejected" ? (
                <>
                  <p className="errorSubtext">{t("SingIn.4")}</p>
                </>
              ) : null} */}
                  <Button type="submit">{t("SingIn.1")}</Button>
                </form>
                <div className="register">
                  <NavLink to={"/singup"} style={{ textDecoration: "none" }}>
                    <p>{t("SingIn.5")}</p>
                  </NavLink>
                </div>
              </About>
              <ImgDiv>
                <Img src={imgs} alt="photo" />
              </ImgDiv>
            </Title>
          </Wrapper>
        </WrapperContainer>
      </WrapperImg>
    </>
  );
}

export default LoginComp;
